import styled from "styled-components";
import { css } from "styled-components";
import { useEffect, useState } from "react";

import { A, Text, Title3 } from "../styles/component";
import Button from "./ButtonMobile";

import { Telegram } from "../images/Socials/Telegram";

const SWrapper = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #0c1e71bb;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;
  z-index: 10000;
  opacity: 0;
  transition: 0.5s ease-in-out;

  ${({ open = false }) =>
    open &&
    `
        opacity: 1;
        pointer-events: all;
    `}
`;

const SModal = styled.div`
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  gap: 20px;

  background: #ffffff;
  box-sizing: border-box;
  padding: 50px 40px;
  border-radius: 43px;
  width: 80vw;
  max-width: 516px;
  min-width: 320px;

  button {
    width: 100%;
    height: 72px;

    font-size: 18px;
    font-weight: 400;

    background: radial-gradient(
      105.43% 294.48% at -10.2% -116.67%,
      #376ce0 0%,
      #2485dd 25.96%,
      #4f4ed9 74.99%,
      #7320cf 96.87%
    );
  }

  @media (max-width: 950px) {
    padding: 30px 20px;
    border-radius: 20px;
    gap: 12px;

    button {
      height: 50px;
      font-size: 14px;
    }
  }
`;

const Close = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  transform: translateX(120%);
  border: 1px solid #ffffff55;
  border-radius: 200px;
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.25s ease-in-out;
  z-index: 1000;

  &:hover {
    border: 1px solid #ffffff88;
    transform: translateX(120%) scale(1.025);
  }

  @media (max-width: 950px) {
    transform: translateX(0);
    border: 2px solid #00000022;
    top: 5%;
    right: 5%;
    width: 30px;
    height: 30px;

    svg,
    path,
    g {
      width: 15px;
      height: 15px;
      stroke: #00000022;
      fill: #00000022;
    }

    &:hover {
      transform: scale(1.025);
    }
  }
`;

const Title = styled(Title3)`
  font-weight: 400;
  font-size: clamp(1.5rem, 4vw, 2.375rem);
  line-height: 110%;

  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%), #ffffff;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;

  @media (max-width: 500px) {
    max-width: 14.063rem;
  }

  margin-bottom: 40px;

  @media (max-width: 950px) {
    margin-bottom: 10px;
  }
`;

const ModalSignUp = ({ open, setOpen }) => {
  useEffect(() => {
    document.querySelector("html").style.overflow = "";
    if (open) document.querySelector("html").style.overflow = "hidden";
  }, [open]);

  return (
    <SWrapper open={open}>
      <SModal>
        <Close
          onClick={() => {
            setOpen(false);
          }}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="21"
            height="21"
            viewBox="0 0 21 21"
            fill="none"
          >
            <path
              d="M1 1L10.5 10.5M10.5 10.5L1 20M10.5 10.5L20 1M10.5 10.5L20 20"
              stroke="white"
              stroke-width="2"
            />
          </svg>
        </Close>
        <Title>
          Выберите <br /> нужный сервис:
        </Title>

        <a
          href="https://data.socialjet.pro/"
          aria-label="Перейти на продукт SocialData"
          target="_blank"
        >
          <Button type="service" colorTxt="#E7F5FF" link={true}>
            SocialData
          </Button>
        </a>

        <a
          href="https://socialjet.pro/socialLead"
          aria-label="Перейти на продукт SocialLead"
          target="_blank"
        >
          <Button type="service" colorTxt="#E7F5FF" link={true}>
            SocialLead
          </Button>
        </a>

        <a
          href="https://telejet.socialjet.pro"
          aria-label="Перейти на продукт TeleJet"
          target="_blank"
        >
          <Button type="service" colorTxt="#E7F5FF" link={true}>
            TeleJet
          </Button>
        </a>

        <a
          href="https://rocketvideo.socialjet.pro"
          aria-label="Перейти на продукт Rocket Video"
          target="_blank"
        >
          <Button type="service" colorTxt="#E7F5FF" link={true}>
            RocketVideo
          </Button>
        </a>

        <a
          href="https://socialjet.pro/socialstream"
          aria-label="Перейти на продукт SocialStream"
          target="_blank"
        >
          <Button type="service" colorTxt="#E7F5FF" link={true}>
            SocialStream
          </Button>
        </a>

        <a
          href="https://socialjet.pro/publishers.html"
          aria-label="Перейти на продукт Для паблишеров"
          target="_blank"
        >
          <Button type="service" colorTxt="#E7F5FF" link={true}>
            Для паблишеров
          </Button>
        </a>
      </SModal>
    </SWrapper>
  );
};

export default ModalSignUp;
